import React from "react";
import { ApolloProvider, useQuery } from "@apollo/client";
import client from "./apollo";
import { InformesType } from "./apollo/types";
import { GET_INFORMES } from "./apollo/queries/InformeQueries";
import styled from "styled-components";
// import Chart from "./components/chart/Chart";
import MyChart from "./components/chart/Chart2";
import List from "./components/list/List";

const Container = styled.div`
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-wrap: wrap;
  overflow-x: hidden;

  div.single-informe {
    margin: 10px 20px;

    ul {
      padding: 0;
      list-style: none;

      b {
        margin-right: 10px;
      }
    }
  }

  g {
    text {
      tspan {
        font-size: 6px !important;
      }
    }
  }
`;

function App(): JSX.Element {
  const { data } = useQuery<InformesType>(GET_INFORMES, {
    client,
  });

  return (
    <div className="App">
      <ApolloProvider client={client}>
        <Container>
          {data?.getInformes && (
            <>
              <MyChart informes={data?.getInformes} />
              <List informes={data?.getInformes} />
            </>
          )}
        </Container>
      </ApolloProvider>
    </div>
  );
}

export default App;
